

.title {
    margin-bottom: 30px;
    position: relative;
    font-family: 'Josefin Sans', sans-serif;
    font-weight: 600;
    font-size: 36px;
    position: relative;
    display: inline-block;
    padding-bottom: 10px;
    z-index: 2;
}

.title h4 {
    margin: 0;

}

.title::before{
    content: '';
    width: 100%;
    height: 1px;
    background: #bcc0d7;
    position: absolute;
    bottom: 0;
    left: 0;
}

.title:after {
    content: '';
    width: 60px;
    height: 1px;
    background: #000000;
    position: absolute;
    bottom: 0;
    left: calc(50% - 30px);
}
*, ::after, ::before {
    box-sizing: border-box;
}


.projects {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.btnStyle {
    position: absolute;
}

@media screen and (max-width: 929px) {
    /*пока максимальная не дойдёт до 929*/
    .projects {
        justify-content: center;
    }
}